export default async function Home() {
  const res = await fetch("http://127.0.0.1:8000/api/services/", {
    cache: "no-store",
  });
  const data = await res.json();
  // Access the 'results' array
  const services = data.results || [];

  return (
    <main>
      <h1>Strengths Innovations</h1>
      <p>We Print What You Think</p>
      <section>
        <h2>Services</h2>
        {/* Use the 'services' array which is now confirmed to be an array */}
        <ul>
          {services.map((s) => (
            <li key={s.id}>
              <h3>{s.title}</h3>
              <p>{s.short}</p>
            </li>
          ))}
        </ul>
      </section>
    </main>
  );
}